.container-fluid {
  padding-left: 0;
}

.slider {
  padding: 0;
}

.slider-control-centerleft,
.slider-control-centerright {
  display: none;
}

.slider-control-bottomcenter {
  padding-bottom: 3rem;
}

.dropdown-toggle:after {
  content: none;
}

.dropdown-item{
    color: white;
}

.dropdown-item:hover{
    color: black;
}

.paging-dot circle{
    fill: white !important;
}